<template>
  <div class="modal-underlay">
    <div class="modal-window">
      <div class='modal-title-wrapper'>
        <div>РЕЗУЛЬТАТ</div>
        <span class="material-icons modal-icon" data-tooltip="На діаграмі зображено розподіл учасників за балами">help_outline</span>
        <span class="material-icons modal-icon" @click='$emit("close")'>close</span>
      </div>
      <div class="modal-body">
        <div class='section'>
          <div class='section-header'>Балів:</div>
          <div class='section-number' :style='`--numfrom: 0; --numto: ${data.score}`'>
            <span class='section-number-max'> / {{data.maxscore}}</span>
          </div>
        </div>
        <span class='vline'></span>
        <div class='section'>
          <div class='section-header'>Місце:</div>
          <div class='section-number' :style='`--numfrom: ${data.marks.length}; --numto: ${data.marks.indexOf(Math.round(ratio*100)) + 1}`'>
            <span class='section-number-max'> / {{data.marks.length}}</span></div>
        </div>
        <div class="progress-bar">
          <progress-bar 
            :ratio='ratio'
            :isTicked='false'
            :isAnimated='true' 
            :colors='["#323232","#ff4d4d","#e1b74d","#4da64d"]'>
          </progress-bar>
        </div>
        <div class="histogram">
          <histogram :distribution='distribution'/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'modal-result',
  props: ['data'],
  computed: {
    ratio() { return this.data.score/this.data.maxscore },
    distribution() {
      let arr = []
      if (this.data.marks) {
        for(var i = 0; i < 10; i++) arr[i] = this.data.marks.filter(a => a > 10*i && a <= 10*(i+1)).length
      } return arr;
    }
  }
}
</script>

<style lang="scss">
@property --num {
  syntax: "<integer>";
  initial-value: 0;
  inherits: false;
}

@keyframes counter {
  0% { --num: var(--numfrom); }
  100% { --num: var(--numto); }
}
.modal-icon {
  position: relative;
  font-weight: normal !important;
  font-size: 2em !important;
  color: lightgray
}
[data-tooltip] {
  &::before {
    z-index: 5;
    opacity: 0;
    content: attr(data-tooltip);
    pointer-events: none;
    position: absolute;
    right: 0; top: 40px;
    padding: 10px 20px;
    border-radius: 4px;
    color: black;
    font: .5em sans-serif;
    background: #fff;
    box-shadow: 0 2px 3px rgba(0, 0, 0, .3);
  }
  &:hover::before { opacity : 1 }
}
.modal-underlay {
  position: fixed;
  z-index: 2;
  left: 0; top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0,.5);
}
.modal-window {
  z-index: 3;
  border-radius: 8px;
  background-color: white;
  width: min(22em,90%);
  box-shadow: 0 5px 10px -2px rgba(0,0,0,.4);
}
.modal-title-wrapper {
  display: flex;
  gap: 10px;
  align-items: center;
  box-shadow: 0 5px 10px -4px rgba(0,0,0,.6); 
  border-radius: 8px 8px 0 0;
  padding: 1em 1.5em;
  background: #323232;
  color: white;
  div {
    font-size: 1.25em;
    flex-grow: 1;
  }
}
.modal-body {
  padding: 1.7em 1.5em 2em 1.5em;
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: space-around;
}
.vline { border-right: 2px solid lightgray; }
.section { 
  width: 40%;
  text-align: center;
}
.section-header { 
  text-transform: uppercase;
  color: gray;
}
.section-number {
  animation: counter 2s linear forwards;
  counter-reset: num var(--num);
  margin: 5px;
  font-size: 2em;
  font-weight: bold;
  &::before { content: counter(num) }
  .section-number-max {
    font-size: .6em;
    color: darkgray
  }
}
.progress-bar {
  width: 75%;
  div span::before {
    content: 'ВІДСОТОК: \A';
    white-space: pre;
    font-size: 1.2em;
    color: gray;
  }
  div span::after { font-size: 3em; }
}
.histogram {
  width: 100%;
  height: 100px;
}
</style>