<template>  
  <div class="histogram">
    <svg viewBox="0 0 300 105">
      <rect v-for='i in distribution.length' :key='i'
        :x='30*(i-1)' y=0 
        width=28 :style='`--ratio: ${(i-1)/10}`'>
        <animate attributeName="height" from=0 :to='100*ratios[i-1]+5' dur=2s fill="freeze" />
      </rect>
      <text v-for='i in distribution.length' :key='i' 
        :x='30*(i-1)+15' :y='100*ratios[i-1]'>{{distribution[i-1]}}</text>
    </svg>
  </div>
</template>

<script>
export default {
  name: 'histogram',
  props: ['distribution','colors'],
  computed: {
    ratios() {
      let max = Math.max(...this.distribution)
      return this.distribution.map(a => a/max)
    }
  },
}
</script>

<style lang="scss" scoped>
@keyframes fill {
  0% { fill: red }
  50% { fill: gold }
  100% { fill: green }
}
.histogram { 
  width: 100%; height: 100%;
}
svg { transform: scale(1,-1) }
rect { animation: fill 2s linear forwards var(--ratio) }
text {
  fill: white;
  font-size: .75em;
  font-weight: bold;
  transform: scale(1,-1);
  transform-box: fill-box;
  transform-origin: center;
  text-anchor: middle;
}
</style>