<template>
  <div class="wrapper">
    <input type='radio' ref='input'
      name='pagination-panel'
      @click="$emit('taskGoto')"/>
    <span class="input-counter" :class='ratio == 0 ? "incorrect" : "correct"'>
      <slot></slot>
    </span>
  </div>
</template>

<script>
export default {
  props: ['ratio'],
  methods: {
    click() { this.$refs.input.click() }
  }
}
</script>

