<template>
  <div class="title-wrapper">
    <side-menu></side-menu>
    <div class="title">{{modules}}</div>
  </div>
  <router-link :to='(levels[i-1].type == "quiz") ? "/quiz" : "/theory"'
      v-for='i in levels.length' :key='i'
      :class='levelIsOpened(i-1) ? "level-block" : "level-block"'
      @click='setCurrentLevelIndex(levels[i-1].index)'>
    <div class="progress-bar-wrapper" :style ='!levels[i-1].score ? `background: lightgray` : (levels[i-1].type == "theory") 
      ? `background: #5F9EA0` : `animation: col 1s -${ratios[i-1]}s linear forwards paused`'>
      <progress-bar :ratio='ratios[i-1]' 
        :colors='["white"]' :isAnimated='false'
        :isTicked='(levels[i-1].type == "theory")'>
      </progress-bar>
    </div>
    <span class="level-block-body">
      <div class="level-name">
        <span>{{(levels[i-1].type == "theory") ? 'Конспект: ' : ''}} {{levels[i-1].name}}</span>
        <span v-if='isMobile'>  ({{Math.round(ratios[i-1]*100)}}%)</span>
      </div>
      <div class="level-info">
        <span class="level-score" :style='levels[i-1].score ? "color: gray" : "color: lightgray"'>
          {{!levels[i-1].score ? 'Не розпочато' : (levels[i-1].type == "quiz") 
          ? `${levels[i-1].score} / ${levels[i-1].maxscore} балів` : 'Переглянуто' }}
        </span>
        <span class="level-stats">
          <span class="material-icons level-stats-icon">people</span>
          <span class="level-score">{{levels[i-1].total ?? 0}}</span>
        </span>
        <span class="level-stats">
          <span class="material-icons level-stats-icon">data_saver_off</span>
          <span class="level-score">{{Math.round(levels[i-1].avg ?? 0)}}%</span>
        </span>
      </div>
    </span>
  </router-link>
</template>

<script>
import sideMenu from '@/components/side-menu.vue'
export default {
  components: { sideMenu },
  computed: {
    isMobile() { return window.innerWidth <= 450 },
    levels() { return this.$store.state.levels },
    modules() { return this.$store.state.modules[localStorage.currentModuleIndex] },
    ratios() { return this.levels.map(a => isNaN(a.score/a.maxscore) ? 0 : a.score/a.maxscore) }
  },
  methods: {
    setCurrentLevelIndex(index) { localStorage.setItem("currentLevelIndex", index) },
    levelIsOpened(index) { return index ? !!this.levels[index-1].score : true }
  }
}
</script>

<style lang="scss" scoped>
.title-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 20px; 
}
.level-block {
  width: 100%;
  height: 6em;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 5px;
  background: white;
  border-radius: 8px;
  border: 1px solid rgba(0,0,0,.1);
}
.disabled {
  pointer-events: none;
  opacity: .5;
}
.level-icon {
  font-size: 3em !important;
  font-weight: 100 !important;
  color: #323232;
  margin: 0 .4em;
}
.level-block-body {
  height: 6em;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-evenly;
  z-index: 1;
  padding-left: 1.4em;
  padding-right: 5px;
  box-shadow: -8px 0 8px -6px rgba(0,0,0,.6);
}
.level-name {
  font-weight: bold; 
  font-size: 1.1em;
  margin-bottom: -5px;
}
.level-info {
  display: flex;
  .level-score {
    color: gray;
    flex-grow: 1;
    margin-right: clamp(10px,2vw,20px);
  }
  .level-stats-icon {
    font-size: 1.25em !important;
    vertical-align: text-bottom;
    color: gray;
    margin-right: 5px;
  }
}
.progress-bar-wrapper {
  height: 6em;
  width: 6em;
  border-radius: 8px 0px 0px 8px;
  background: white;
  font-size: 1em;
  @media (max-width: 450px) { width: 15px }
  div {
    width: 75%;
    height: 75%;
    margin: 12.5%;
    font-size: 1.1em !important;
    @media (max-width: 450px) { display: none }
  }
}
</style>