<template>
<div id="wrapper">
  <svg viewBox="0 0 120 120">
    <circle class='circle1' r=50 cx=60 cy=60 :style='{stroke: color}'></circle>
    <circle class='circle2' r=50 cx=60 cy=60 :style='`--ratio: ${ratio}`'></circle>
  </svg>
  <span class='counter' :style='`--ratio: ${ratio}; color: ${color}`'></span>
</div>
</template>

<script>
export default {
  name: 'progress-bar-sectioned',
  props: ['ratio', 'color']
}
</script>

<style lang="css" scoped>
@property --num {
  syntax: "<integer>";
  initial-value: 0;
  inherits: false;
}
#wrapper { 
  position: relative;
}
.counter {
  animation: counter 2s linear forwards var(--ratio);
  counter-reset: num var(--num);
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.25em;
}
.counter::before { 
  content: counter(num); 
  font-weight: bold;
}
.counter::after {
  content: '%'
}
@keyframes counter {
  0% { --num: 0; }
  100% { --num: 100; }
}
@keyframes dash {
  0% { stroke-dashoffset: 314 }
  100% { stroke-dashoffset: 0 }
}
.circle1 {
  stroke-width: 5px; 
  stroke-opacity: .12;
}
.circle2 {
  stroke: radial-gradient(#fff 68%, transparent 68% 70%, #fff 70%),
    repeating-conic-gradient(#000 0% 3%, transparent 3% 5%);
  stroke-width: 7px; 
  stroke-dasharray: 30 1.4 30 1.4 30 1.4 30 1.4 30 1.4 30 1.4 30 1.4 30 1.4 30 1.4 30 1.4
}
</style>