<template>
<div class="wrapper">
  <svg viewBox="0 0 110 110">
    <circle class='circle1' r=50 cx=55 cy=55 :style='{stroke: colors[0]}'></circle>
    <circle class='circle2' r=50 cx=55 cy=55 :style='`
      --ratio: ${ratio};
      --lento: ${Math.round(ratio*314)};
      --duration: ${2*isAnimated}s;
      --col2: ${colors[1] ?? colors[0]};
      --col3: ${colors[2] ?? colors[0]};
      --col4: ${colors[3] ?? colors[0]};`'></circle>
  </svg>
  <span v-if='!isTicked' class='counter' :style='`
    --duration: ${2*isAnimated}s; 
    --numfrom: 0; --numto: ${Math.round(100*ratio)};
    color: ${colors[0]}`'></span>
  <span v-else class='material-icons' :style='{color: colors[0]}'>{{(ratio > 0) ? 'done' : 'close'}}</span>
</div>
</template>

<script>
export default {
  name: 'progress-bar',
  props: ['isTicked', 'isAnimated', 'ratio', 'colors']
}
</script>

<style lang="scss" scoped>
@property --num {
  syntax: "<integer>";
  initial-value: 0;
  inherits: false;
}
.wrapper { 
  position: relative;
  width: 100%; 
  height: 100%; 
}
.material-icons {
  color: white;
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
}
.counter {
  animation: counter var(--duration) linear forwards;
  counter-reset: num var(--num);
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  &::after {
    content: counter(num) '%'; 
    font-weight: bold;
  }
}
@keyframes counter {
  0% { --num: var(--numfrom); }
  100% { --num: var(--numto); }
}
@keyframes dash {
  0% { stroke-dashoffset: var(--lento) }
  100% { stroke-dashoffset: 0 }
}
@keyframes strokecol {
  0% { stroke: var(--col2) }
  50% { stroke: var(--col3) }
  100% { stroke: var(--col4) }
}
.circle1 {
  stroke-width: 5px; 
  stroke-opacity: .12;
}
.circle2 {
  stroke: white;
  stroke-width: 5px;
  stroke-linecap: round;
  stroke-dasharray: var(--lento) 314;
  animation: dash var(--duration) linear forwards, strokecol calc(var(--duration) / var(--ratio)) linear forwards var(--ratio);
}
</style>